  width: 26px;
}

.svg-svg-arrow-down:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-arrow-down-dims {
  width: 26px;
  height: 26px;
}

.svg-svg-city {
  background-position: 61.9047619048% 0;
  background-size: 261.5384615385% auto;
