  line-height: 0;
  padding-top: 100%;
}

.svg-svg-next-dims {
  width: 16px;
  height: 16px;
}

.svg-svg-prev {
  background-position: 100% 44.4444444444%;
  background-size: 425% auto;
  width: 16px;
}

.svg-svg-prev:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
