}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "Inter", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
  font-size: 11px;
  line-height: 145%;
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  body {
    font-size: 12px;
    line-height: 167%;
  }
}
@media screen and (min-width: 1366px) {
  body {
    font-size: 14px;
    line-height: 171%;
  }
}
@media screen and (min-width: 1600px) {
  body {
    font-size: 15px;
    line-height: 173%;
  }
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.main {
  flex-grow: 1;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: var(--maxWidth);
  padding: 0 var(--padding);
  margin: 0 auto;
}

.btn {
  width: 36px;
  min-width: 36px;
  height: 36px;
  padding: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  background: var(--accent);
  overflow: hidden;
  transition: all 0.2s ease-in-out;
}
.btn svg {
  width: 26px;
  height: 26px;
}
.btn:hover, .btn:focus {
  background: #C82F29;
}
.btn:active {
  background: #AF2722;
}

.link {
  padding: 12px 20px;
  background: var(--accent);
  border-radius: 3px;
  border: 1px solid var(--accent);
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
  text-align: center;
  color: #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease-in-out;
}
.link--send {
  padding: 15px 20px 16px;
  font-size: 15px;
  width: 100%;
}
.link:hover, .link:focus {
  background: #C82F29;
  border-color: #C82F29;
}
.link:active {
  background: #AF2722;
  border-color: #AF2722;
}
.link.link--disable {
  background: #C5C5C5;
  border-color: #C5C5C5;
}
.link.link--default {
  color: var(--textColor);
  background: transparent;
  border: 1px solid var(--textColor);
}
.link.link--default:hover, .link.link--default:focus {
  background: #C82F29;
  color: #fff;
  border-color: #C82F29;
}
.link.link--default:active {
  background: #AF2722;
  color: #fff;
  border-color: #AF2722;
}
.link.link--blocked {
  color: #BABABA;
  border: 1px solid #BABABA;
  background: transparent;
}

.title {
  font-weight: 700;
  font-size: 20px;
  line-height: 120%;
}

.block {
  padding: 15px;
  border: 5px solid var(--bgColor);
}

.lead {
  margin-bottom: 14px;
  font-weight: 300;
  font-size: 12px;
  line-height: 150%;
  color: var(--textColor);
}
.lead p {
  margin-bottom: 16px;
}
.lead p:last-child {
  margin-bottom: 0;
}

.elements {
  padding: 30px var(--padding);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.elements a {
  margin-bottom: 10px;
}

.select2 {
  width: 100% !important;
}
.select2-selection {
  width: 100%;
  font-size: 15px;
  font-weight: 400;
}
.select2-selection__placeholder {
  font-weight: 400;
  font-size: 15px;
  line-height: 100%;
  color: #000 !important;
}
.select2-search__field {
  display: none;
}
.select2-results__option--selectable.select2-results__option--highlighted {
  background: var(--bgColor) !important;
  color: #000 !important;
}
.select2-selection--single {
  height: 50px !important;
  border: none !important;
  border-radius: 3px !important;
}
.select2-selection__rendered {
  width: 100%;
  height: 50px !important;
  line-height: 48px !important;
  padding: 0 40px 0 22px !important;
  border-radius: 3px;
  border: 1px solid transparent;
  background: var(--bgColor) !important;
  position: relative;
  transition: all 0.2s ease-in-out !important;
}
.select2-selection__rendered::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background: url("../images/icons/dropdown-arrow.svg") no-repeat center/contain;
}
.select2-selection__rendered:hover, .select2-selection__rendered:focus {
  background: #EEF2F5 !important;
}
.select2-container--open .select2-selection__rendered {
  border: 1px solid #878787;
  background: #EEF2F5 !important;
}
.select2-container--open .select2-selection__rendered::after {
  transform: translateY(-50%) rotate(180deg);
}
.select2-selection__arrow {
  display: none;
}
.select2-search--dropdown {
  padding: 0;
}
.select2-dropdown {
  top: 4px;
  border: 1px solid #DADADA;
  border-top: 1px solid #DADADA !important;
  filter: drop-shadow(0px 10px 15px rgba(0, 0, 0, 0.1));
  border-radius: 3px;
}
.select2-results__option {
  height: 36px;
  padding: 6px 22px;
  font-weight: 400;
  font-size: 13px;
  line-height: 25px;
}
.select2-results__option:hover, .select2-results__option:focus {
  background: var(--bgColor);
}

@media screen and (min-width: 1024px) {
  .title {
    font-size: 32px;
    line-height: 125%;
  }

  .block {
    padding: 25px;
  }

  .lead {
    font-size: 16px;
    line-height: 138%;
  }
  .lead p {
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 1366px) {
  .block {
    padding: 35px;
  }

  .lead {
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 1600px) {
  .title {
    font-size: 42px;
  }

  .lead {
    margin-bottom: 30px;
    line-height: 162%;
  }
  .lead p {
    margin-bottom: 25px;
  }
}
.header {
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
.header-wrapper {
  min-height: 56px;
}
.header--without .header__inner::after {
  display: none;
}
.header--without .header__inner--active::after {
  display: block;
}
.header__lead {
  display: none;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__right {
  width: calc(100% - 48px);
}
.header__logo {
  min-width: 150px;
  font-weight: 700;
  font-size: 8px;
  line-height: 8px;
  text-transform: uppercase;
}
.header__logo img {
  display: block;
  margin-bottom: 4px;
}
.header__logo br {
  display: none;
}
.header__info {
  display: flex;
  align-items: center;
}
.header__contacts {
  display: none;
}
.header__contacts-right {
  display: none;
}
.header__contacts-tel {
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  text-transform: uppercase;
  color: #000000;
}
.header__contacts-tel__icon {
  width: 36px;
  height: 36px;
  display: none;
  background: var(--bgColor);
  border-radius: 3px;
}
.header__contacts-time {
  font-weight: 500;
  font-size: 10px;
  line-height: 120%;
  color: var(--secondary);
}
.header__user {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.header__btn {
  margin-right: 8px;
}
.header__btn:last-child {
  margin-right: 0;
}
.header__btn--tel {
  background: transparent;
  border: 1px solid var(--textColor);
}
.header__btn--tel:hover, .header__btn--tel:focus {
  background: transparent;
}
.header__btn-text {
  display: none;
}
.header__bottom {
  padding: 14px 30px 30px;
  height: calc(100vh - 56px);
  position: fixed;
  transform: translateX(-100%);
  z-index: 10;
  top: 56px;
  left: 0;
  right: 0;
  background: #fff;
  overflow-y: auto;
  transition: transform 0.2s ease;
}
.header__bottom-contacts--tel {
  background: transparent;
  border: 1px solid var(--textColor);
  color: var(--textColor);
}
.header__bottom-contacts li {
  margin-bottom: 14px;
}
.header__bottom-contacts li:last-child {
  margin-bottom: 0;
}
.header__bottom-contacts .link {
  width: 100%;
  padding: 17px 20px;
  min-height: 51px;
  font-size: 15px;
  font-weight: 600;
}
.header__menu {
  margin-bottom: 24px;
}
.header__menu-toggle {
  display: block;
  width: 100%;
  padding: 16px 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  text-transform: uppercase;
  transition: color 0.2s ease;
}
.header__menu-toggle--bold {
  font-weight: 700;
}
.header__menu-toggle--submenu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu-toggle--submenu span {
  width: calc(100% - 34px);
}
.header__menu-toggle--submenu svg {
  width: 24px;
  height: 24px;
  fill: var(--secondary);
  transform: rotate(180deg);
}
.header__menu-toggle:hover, .header__menu-toggle:focus {
  color: var(--accent);
}
.header__menu-item {
  border-bottom: 1px solid var(--bgColor);
}
.header__menu-item:last-child {
  border-bottom: none;
}
.header__menu-item--active svg {
  transform: rotate(0);
  fill: var(--accent);
}
.header__menu-item--active .header__menu-submenu {
  display: block;
}
.header__menu-submenu {
  display: none;
  padding-bottom: 16px;
  font-weight: 400;
  font-size: 13px;
  line-height: 200%;
  color: var(--textColor);
  transition: all 0.2s ease-in-out;
}
.header__burger {
  margin-right: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__burger-inner {
  width: 16px;
  height: 14px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: transform 0.2s ease-in-out;
}
.header__burger-line {
  width: 16px;
  height: 1px;
  background: #fff;
  border-radius: 1px;
  transform-origin: 0% 0%;
  transition: all 0.2s ease-in-out;
}
.header__burger-line--2 {
  transition: transform 0.2s ease-in-out;
}
.header__inner {
  padding: 10px var(--padding);
  display: flex;
  align-items: center;
