}
.vacancy__btn {
  font-size: 11px;
  line-height: 127%;
  color: var(--accent);
  text-decoration: underline;
}
.vacancy__toggle {
  width: 36px;
  min-width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--bgColor);
  border-radius: 3px;
  position: relative;
  transition: background 0.3s ease;
}
.vacancy__toggle::before, .vacancy__toggle::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 24px;
  transition: opacity 0.2s ease, transform 0.2s ease;
}
.vacancy__toggle::before {
  background: url("../images/icons/btn-arrow-2.svg") no-repeat center/contain;
  opacity: 1;
}
.vacancy__toggle::after {
  background: url("../images/icons/btn-arrow.svg") no-repeat center/contain;
  opacity: 0;
  transform: rotate(-90deg) translate(50%, -46%);
}
.vacancy__info {
  padding: 15px;
  font-weight: 300;
  font-size: 10px;
  line-height: 140%;
  color: var(--textColor);
  border-top: 1px solid var(--bgColor);
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  will-change: max-height;
  transition: all 0.2s ease-out;
}
.vacancy__info-box {
  margin-bottom: 10px;
}
.vacancy__info-box:last-child {
  margin-bottom: 0;
}
.vacancy__info-title {
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 12px;
  line-height: 167%;
  color: #000;
}
.vacancy__info-list li {
  margin-bottom: 5px;
  padding-left: 8px;
  position: relative;
}
.vacancy__info-list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 6px;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background: #000;
}
.vacancy__info-list li:last-child {
  margin-bottom: 0;
}
.vacancy__item {
  margin-bottom: 10px;
  border: 3px solid var(--bgColor);
  border-radius: 3px;
}
.vacancy__item:last-child {
  margin-bottom: 0;
}
.vacancy__item--open .vacancy__info {
  max-height: none;
  padding: 20px;
  opacity: 1;
  box-sizing: content-box;
}
.vacancy__item--open .vacancy__toggle {
  background: var(--accent);
}
.vacancy__item--open .vacancy__toggle::before {
  opacity: 0;
  transform: rotate(90deg) translate(-50%, 50%);
}
.vacancy__item--open .vacancy__toggle::after {
  opacity: 1;
  transform: rotate(0deg) translate(-52%, -45%);
}
.vacancy__item--open .vacancy__btn::before {
  content: "";
}
.vacancy__item--open .vacancy__btn span {
  display: none;
}

